import simpleAddress from '@/components/simple-address/simple-address.nvue';
import { checkMobile,getWindowResize } from '@/common/utils.js'
export default{
	components:{
		simpleAddress
	},
	data(){
		return {
			cityPickerValueDefault: [0, 0, 1],
			address:{
				addressCode:null,
				addressName:null,
			},
			detailAddress:'',
			name:'',
			mobile:'',
			fixedBtn:true,
			windowHeight:'',
		}
	},
	onLoad() {
		//监听窗口高度变化（为了兼容h5端底部按钮被键盘顶起的问题）
		this.windowResize()
	},
	methods:{
		//点击弹窗选择地址
		addressChange(){
			this.$refs.simpleAddress.open();
		},
	    //确认选择地址
		addressConfirm(e){
			// this.cityPickerValueDefault = e.value 保留当前选择
			this.address = {
				addressCode:e.areaCode,
				addressName:e.label
			}
		},
		//确定
		submit(){
			this.judgeFun().then(()=>{
				let params = {
					name:this.name,
					addressName:this.address.addressName,
					detailAddress:this.detailAddress,
					mobile:this.mobile,
				}
				uni.$emit('address',params)
				uni.navigateBack()
			}).catch((err)=>{})
		},
		//验证
		judgeFun(){
			return new Promise((resolve,reject)=>{
				let tip = ''
				if(!this.address.addressName){
					tip = '请选择通讯地址'
					reject()
				}else if(!this.detailAddress){
					tip = '请输入详细地址'
					reject()
				}else if(!this.name){
					tip = '请输入姓名'
					reject()
				}else if(!checkMobile(this.mobile)){
					tip = '请输入正确的手机号'
					reject()
				}else{
					resolve()
				}
				if(tip){
					uni.showToast({
						icon:'none',
						title:tip
					})
				}
			})
		},
		//监听窗口高度变化
		windowResize(){
			this.windowHeight = getWindowResize('height')
		    uni.onWindowResize((res) => {
		        if(res.size.windowHeight < this.windowHeight){
		            this.fixedBtn = false
		        }else{
		            this.fixedBtn = true
		        }
		    })
		},
		//显示按钮
		showBtn(){
			this.fixedBtn = true
		},
		//隐藏按钮
		hideBtn(){
			this.fixedBtn = false
		}
	}
}
